import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Route, Routes } from "react-router-dom";
import { Navigate, useNavigate, useLocation } from "react-router-dom";

// Redux
import { persistor } from "./redux/Store";
import * as authActions from "./redux/modules/AuthSlice";

//  import
import {
    Login
} from "./pages/paths";

// Custom Hooks

// Components
import Sidebar from "./components/Sidebar";

// CSS
import "./styles/common.css";

const Root = () => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const location = useLocation();

    //   (useState)
    const [hasLoginData, setHasLoginData] = useState(false);
    const [showSidebar, setShowSidebar] = useState(true);   //     (: true)

    // Redux  
    const { errorCode } = useSelector((state) => state.constant);
    const { userData } = useSelector((state) => state.auth);
    

    //     (componentDidMount)
    useEffect(() => {
        console.log("!--- Root ComponentDidMount ---!");

        const accessToken = localStorage.getItem("accessToken");

        if (accessToken) {
            setHasLoginData(true);
        }
    }, []);

    // Redux state    (componentDidUpdate )
    useEffect(() => {
        const loginData = localStorage.getItem("accessToken");
        if ((loginData && !hasLoginData)) {
            setHasLoginData(true);
        } else if (!loginData && hasLoginData) {
            setHasLoginData(false);

            navigate("/login");
        }
    }, [userData]);

    //    
    const toggleSidebar = () => {
        setShowSidebar(!showSidebar);
    };

    return (
        <div id="wrap">
            <div className="container">
                { /*   sidebar  */
                    /*hasLoginData && */showSidebar && <Sidebar toggleSidebar={toggleSidebar} />
                }

                <div className={`content ${showSidebar ? "content-with-sidebar" : ""}`}>
                    <Routes>
                        <Route path="/" element={
                            /*     /login   */
                            !hasLoginData ? <Navigate to="/login" replace />
                                : <h1>Login Success</h1> } />
                        <Route path="/login" element={ <Login /> } />
                    </Routes>
                </div>
            </div>
        </div>
    );
}

export default Root;